import React from 'react'
import styled from '@emotion/styled'
import { BgImage } from 'gbimage-bridge'
import { graphql, useStaticQuery } from 'gatsby'
import { getImage } from 'gatsby-plugin-image'

import Box from '../../../components/Box'
import Text from '../../../components/Text'
import Button from '../../../components/Button'
import { responsive } from '../../../contexts/responsive'



const GatsbyBgImage = styled(BgImage)`
height: 100%;
width: 100%;
`

const Care = ({ fullpageApi, onLoad = () => {} }) => {
  const { desk } = useStaticQuery(
    graphql`
      query CareQuery {
        desk: file(relativePath: { eq: "data_bg.png" }) {
          childImageSharp {
            gatsbyImageData(
              placeholder: BLURRED
              width: 1920
            )
          }
        }
      }
    `
  )
  const imageDesk = getImage(desk)
  return (
    <Box className="section">
      <Box.Absolute height="100%" greaterThanOrEqual="md" top="0" left="0" right="0">
        <GatsbyBgImage image={imageDesk} onLoad={onLoad} />
      </Box.Absolute>
      <Box.Relative color="white" mx={responsive('auto', 'unset')} px={responsive('1em', '8.875em')} maxWidth={responsive('20.75em', '64em')}>
        <Text fontWeight={'black'} letterSpacing={responsive('0.25em', '0.5rem')} fontSize={responsive('1.875em', '3.75em')} mr={responsive(0, '3em')}>為什麼要幫珊瑚礁進行體檢？</Text>
        <Text mt={responsive('0.75rem', '0.33em')} fontSize={responsive('1em', '1.625em')}>
          珊瑚礁生態系對於海洋的重要性就好比陸地上的熱帶雨林生態系，雖然面積只佔全球的千分之二，卻提供大海裡將近四分之一的物種覓食與棲息的環境。<br />
          珊瑚礁的興衰也和居民的健康與安全息息相關，除了帶來龐大的觀光商機之外，在生技產業也是炙手可熱的來源、立體的結構能夠避免海岸線遭受侵蝕，省下保護工程的費用、透過的大地的力量讓我們能夠在海灘附近嬉戲。<br />
          但在近年來，珊瑚礁面臨著來自四面八方的威脅，也因此國際間展開了珊瑚礁體檢計畫，除了監測環境的變化之外，也試圖守護這片海洋的美麗。
        </Text>
        <Box mt="1.5em">
          <Button.Outline
            fontSize={responsive('1em', '2.25em')}
            px={responsive('0.875em', '1em')}
            py={responsive('0.5em', '0.4em')}
            onClick={() => fullpageApi.moveSectionDown()}
            filter="drop-shadow(4px 5px 5px black)"
          >點擊了解更多珊瑚礁體檢計畫</Button.Outline>
        </Box>
      </Box.Relative>
      <Box.Absolute bottom="1em" right={responsive('1em', '2em')} color="white">攝影師：謝宇新攝</Box.Absolute>
    </Box>
  )
}

export default Care
