import React, { useEffect, useRef, useState } from 'react'
import {
  Modal,
  ModalContent,
  useDisclosure,
  Divider,
  Center,
  HStack
} from "@chakra-ui/react"
import { graphql, useStaticQuery } from 'gatsby';
import { BgImage } from "gbimage-bridge"
import { getImage } from 'gatsby-plugin-image';
import { keyframes } from '@emotion/react'
import isArray from 'lodash/isArray';
import { BsHandIndexThumb, BsArrowLeft, BsArrowRight } from "react-icons/bs";
import ReactHoverObserver from 'react-hover-observer';
import styled from '@emotion/styled';

import Box from '../../../components/Box'
import Text from '../../../components/Text'
import Flex from '../../../components/Flex'
import BackgroundImage from '../../../components/BackgroundImage'
import Animation from '../../../components/FloatAnimation'
import Bubbles from '../../../components/Bubbles';
import { Media, responsive } from '../../../contexts/responsive'

import GuideModal from './GuideModal';
import { animals } from '../../data';

import animals_bubble from './animals_bubble.png'

// import { headerHeight } from '../../contexts/header/Provider'

const anime = keyframes`
  0%, 50% {
    width: 80%
  }
  25%, 75% {
    width: 100%
  }
`

const BreathBubble = styled(BackgroundImage)`
  animation: 5s ${anime} infinite;
`

const GatsbyBgImage = styled(BgImage)`
height: 100%;
width: 100%;
`

const labels = ['身長', '特徵', '生物角色', '捕捉目的']
// const isLightHouse = typeof window !== 'undefined' && (/Android.+Lighthouse/i.test(window.navigator.userAgent))

const Animals = ({ fullpageApi, isActive, onLoad = () => {} }) => {
  const hasOpened = useRef()
  const { desk } = useStaticQuery(
    graphql`
      query animalsquery {
        desk: file(relativePath: { eq: "bg_far.png" }) {
          childImageSharp {
            gatsbyImageData(
              placeholder: BLURRED
              width: 1920
            )
          }
        }
      }
    `
  )
  const imageDesk = getImage(desk)
  const [first, setStatus] = useState(true)
  const [open, setGuideOpen] = useState(false)
  const [show, setdes] = useState(false)
  const [species, setSpe] = useState()
  const { isOpen, onOpen, onClose } = useDisclosure()
  useEffect(() => {
    if (isActive && first) {
      setTimeout(() => onOpen(), 500)
      setTimeout(() => setdes(true), 2750)
    }
  }, [isActive])

  useEffect(() => {
    if (isOpen || open) {
      hasOpened.current = true
      fullpageApi?.setAllowScrolling(false)
      setTimeout(() => onClose(), 5000)
    } else {
      if (hasOpened.current) setStatus(false)
      fullpageApi?.setAllowScrolling(true)
    }
  }, [isOpen, open])
  // console.log(species)
  const animalsRender = (
    <Box
      pos="relative"
      // left={responsive('50%', 0)}
      // transform={responsive('translateX(-45%)', 'none')}
      width={responsive('1920px', 'auto')}
      height="100vh"
      overflow="hidden"
      // bg="#005d91"
    >
      {/* <StaticImage src="../bg_far.png" /> */}
      <Box height="100%">
        <GatsbyBgImage image={imageDesk} onLoad={onLoad} />
      </Box>
      <Bubbles isActive={isActive} />
      {animals.map((d, i) => (
        <ReactHoverObserver key={i}>
          {({ isHovering }) => (
            <Box.Absolute
              opacity={first ? 0 : 1}
              cursor="pointer"
              top={d.top}
              bottom={d.bottom}
              left={d.left}
              right={d.right}
              width={d.width}
              transition="all 1s"
              onClick={() => {
                setGuideOpen(i.toString())
                setSpe(d.species)
              }}
            >
              {d.species === '魚類' ? (
                <Animation delay={`${i * -0.5}s`}>
                  <BackgroundImage src={d.src} ratio={d.ratio} />
                </Animation>
              ) : <BackgroundImage src={d.src} ratio={d.ratio} />}
              {isHovering && (
                <Box.Absolute
                  zIndex={99}
                  borderRadius="1.5rem"
                  p="1em"
                  width={responsive('50%', '18em')}
                  bg="white"
                  top="15%"
                  left="95%"
                  {...d.info}
                >
                  <Text.Bold whiteSpace="pre-wrap" fontSize={responsive('1em', '2em')}>{d.name}</Text.Bold>
                  <Text whiteSpace="pre-wrap" fontSize={responsive('1em', '1.25em')}>{d.eng}</Text>
                  {d.data && d.data.map((data, k) => (
                    <Box key={k}>
                      <Divider mt="0.25em" mb="0.375em" borderBottom="1px solid" opacity="1" />
                      <Flex alignItems="center">
                        <Box width="5em">{labels[k]}</Box>
                        <Flex alignItems="center" flex={1}>
                          {isArray(data.icon) ? data.icon.map((icon, key) => (
                            <Flex alignItems="center" ml={key && '0.25em'} key={key}>
                              <Box color="black" width="1.875em" mr="0.25rem">
                                {icon}
                              </Box>
                              <Text.Bold fontSize={responsive('1em', '1.125em')}>{data.text[key]}</Text.Bold>
                            </Flex>
                          )) : data.icon && (
                            <Box color="black" width="1.875em" mr="0.25rem">
                              {data.icon}
                            </Box>
                          )}
                          {!isArray(data.text) && <Text.Bold fontSize={responsive('1em', '1.125em')}>{data.text}</Text.Bold>}
                        </Flex>
                      </Flex>
                    </Box>
                  ))}
                  <Center>
                    <Box.Inline px="1.875em" py="0.5em" borderRadius={'full'} border="1px solid" mt={responsive('0.75em', '1em')}>詳細介紹</Box.Inline>
                  </Center>
                </Box.Absolute>
              )}
            </Box.Absolute>
          )}
        </ReactHoverObserver>
      ))}
    </Box>
  )
  return (
    <Box className="section">
      <Media lessThan="md">
        <Box className="normalScroll" data-inital-scroll="0.35" data-fp overflowX="scroll">
          {animalsRender}
        </Box>
      </Media>
      <Media greaterThanOrEqual="md">
        {animalsRender}
      </Media>
      <Modal
        autoFocus={false}
        size="sm"
        isCentered
        isOpen={isOpen}
        onClose={onClose}
      >
        <ModalContent
          bg="transparent"
          boxShadow={'none'}
          pointerEvents="none"
        >
          <BreathBubble position="absolute" transform="translate(-50%, -50%)" left="50%" top="50%" width={responsive('13.125em', '80%')} src={animals_bubble} ratio={1} />
          <Center as={Media} lessThan="md" height="100%" color="white" flexDirection={'column'} display={show ? 'none' : 'flex'} transition="all 0.5s">
            <HStack justifyContent={'center'} spacing={5}>
              <BsArrowLeft />
              <BsArrowRight />
            </HStack>
            <Text mt="0.5em" fontSize={responsive('1.5em', '2.125em')}>左右滑動</Text>
            <Text fontSize={responsive('1.125em', '1.5em')}>查看行經生物</Text>
          </Center>
          <Center height="100%" color="white" flexDirection={'column'} as={Media} lessThan="md" display={show ? 'flex' : 'none'} transition="all 0.5s">
            <BsHandIndexThumb size="3em" />
            <Text mt="0.5em" fontSize={responsive('1.5em', '2.125em')}>點擊生物</Text>
            <Text fontSize={responsive('1.125em', '1.5em')}>查看完整資訊</Text>
          </Center>
          <Center height="100%" color="white" flexDirection={'column'} as={Media} greaterThanOrEqual="md">
            <BsHandIndexThumb size="3em" />
            <Text mt="0.5em" fontSize={responsive('1.5em', '2.125em')}>點擊生物</Text>
            <Text fontSize={responsive('1.125em', '1.5em')}>查看完整資訊</Text>
          </Center>
        </ModalContent>
      </Modal>
      <GuideModal species={species} setSpe={setSpe} isOpen={open} onOpen={setGuideOpen} />
    </Box>
  )
}

export default Animals
