import React from 'react'
import { Center } from '@chakra-ui/react'
import { StaticImage } from 'gatsby-plugin-image'
import { graphql, useStaticQuery } from 'gatsby';
import { BgImage } from "gbimage-bridge"
import { getImage } from 'gatsby-plugin-image';
import styled from '@emotion/styled';

import Box from '../../components/Box'
import Button from '../../components/Button'
import Flex from '../../components/Flex'
import { Media, responsive } from '../../contexts/responsive'

const GatsbyBgImage = styled(BgImage)`
height: 100%;
width: 100%;
`

const Epilogue = ({ onLoad = () => {} }) => {
  const { desk } = useStaticQuery(
    graphql`
      query epiloguequery {
        desk: file(relativePath: { eq: "bg_epilogue.jpeg" }) {
          childImageSharp {
            gatsbyImageData(
              placeholder: BLURRED
              width: 1920
            )
          }
        }
      }
    `
  )
  const imageDesk = getImage(desk)
  return (
    <Box className='section' bgGradient={'linear(to-r, black, white)'}>
      <Box.FullAbs>
        <Box as={Media} greaterThanOrEqual='md' height="100%"><GatsbyBgImage image={imageDesk} onLoad={onLoad} /></Box>
        <Box as={Media} lessThan='md' height="100%"><GatsbyBgImage image={imageDesk} onLoad={onLoad} /></Box>
      </Box.FullAbs>
      <Center pos={'relative'} flexDir={'column'}>
        <Box as={Media} fontWeight={900} fontSize="3.75em" color="white" greaterThanOrEqual='md'>
          我們相信，唯有深刻了解，<br />
          才會產生關懷並採取行動
        </Box>
        <Box as={Media} fontWeight={900} fontSize="1.875em" color="white" lessThan='md'>
          我們相信，<br />唯有深刻了解，<br />
          才會產生關懷<br />並採取行動
        </Box>
        <Flex flexDir={'column'} textAlign={'center'} mt={responsive('1.5em', '3.5em')} >
          <Button _hover={{ bg: 'transparent '}} _active={{ bg: 'transparent '}} fontSize={responsive('1em', '2em')}>點擊查看完整體檢計畫報告</Button>
          <Button _hover={{ bg: 'transparent '}} _active={{ bg: 'transparent '}} fontSize={responsive('1em', '2em')} mt={responsive('1.25em', '1em')} href="https://teia.tw/ocean_lastestpost/2021%e7%8f%8a%e7%91%9a%e7%a4%81%e9%ab%94%e6%aa%a2%e8%a1%8c%e7%a8%8b-2/">我要報名體檢志工</Button>
        </Flex>
      </Center>
      <Box.Absolute bottom="1em" right={responsive('1em', '2em')} color="white">攝影師：謝宇新攝</Box.Absolute>
    </Box>
  )
}

export default Epilogue
