const anchors = ["", "preface", "intro", "aniamls", "care", "plan", "data", "detail", "equipment", "step", "survey", "epilogue", "about"];

const variants = {
  visible: { opacity: 1, x: [0, 100, 0] },
  hidden: { opacity: 0 },
}

const pages = [Preface, Intro, Animals, Care, IntroPlan, Data, Detail, Equipment, Step, Survey, Epilogue]

const GatsbyBgImage = styled(BgImage)`
height: 100%;
width: 100%;
`

const CoverPage = ({ fullpageApi, onLoad, isActive }) => {
  const { desk } = useStaticQuery(
    graphql`
      query CoverQuery {
        desk: file(relativePath: { eq: "bg.png" }) {
          childImageSharp {
            gatsbyImageData(
              placeholder: BLURRED
              width: 1920
            )
          }
        }
      }
    `
  )
  const imageDesk = getImage(desk)
  return (
    <Box.Relative className="section">
      <Box.Absolute height="100%" as={Media} greaterThanOrEqual="md" top="0" left="0" right="0">
        <GatsbyBgImage image={imageDesk} onLoad={onLoad} />
      </Box.Absolute>
      <Box.Absolute as={Media} lessThan="md" top="0" left="0" right="0">
        <BackgroundImage backgroundPosition="71% 50%" src={bg_mobile} height="100vh" ratio={768 / 736} onLoad={onLoad} />
      </Box.Absolute>
      <Bubbles isActive={isActive} />
      <Box.Absolute top="11.85%" left={responsive('52%', "8.7%")} transform={responsive('translateX(-50%)', 'none')} width={responsive('89%', "45.2%")}>
        <BackgroundImage src={title} ratio={1735 / 961} />
      </Box.Absolute>
      <Box.Absolute left={responsive('10%', '11%')} bottom={responsive('45%', '12%')}>
        <Button.Outline
          onClick={() => {
            fullpageApi.moveSectionDown()
            // setShow(true)
          }}
          fontSize={responsive('1em', '2.25em')}
          p={responsive('0em 1.25em 0em 1em', '0em 2.875em 0em 2.25em')}
          py={responsive('0.25em', '0.5625em')}
          iconSpacing="0"
          leftIcon={<IoIosArrowDown size="1.5em" />}
          filter="drop-shadow(4px 5px 5px black)"
        >
          往下滑查看完整介紹
        </Button.Outline>
      </Box.Absolute>
    </Box.Relative>
  )
}

const handleInitalScroll = (fpApi) => {
  const debugMode = window.location.search.includes('debug')
  document.querySelectorAll('.normalScroll').forEach(e => {
    const { initalScroll, fp } = e.dataset
    if (initalScroll) {
      e.scroll(e.scrollWidth * initalScroll, 0)
    }
    if (fp) {
      let info
      if (debugMode) {
        info = document.createElement('div')
        info.style.position = 'absolute'
        info.style.color = 'white'
        info.style.top = '5em'
        info.style.left = '1em'
        info.style.right = '1em'
        info.style.pointerEvents = 'none'
        info.style.whiteSpace = 'pre-wrap'
        e.parentNode.append(info)
      }

      const touches = {
        "touchstart": { "x": -1, "y": -1 },
        "touchmove": { "x": -1, "y": -1 },
        "touchend": false,
        "direction": "undetermined"
      }
      const touchHandler = eventType => event => {
        let touch;
        if (typeof event !== 'undefined') {

          // event.preventDefault();
          if (typeof event.touches !== 'undefined') {
            touch = event.touches[0];
            // if (debugMode) {
            //   info.innerHTML = `${eventType}: ${JSON.stringify(touches, null, 2)}`
            // }
            switch (eventType) {
              case 'touchstart':
              case 'touchmove':
                touches[eventType].x = touch.pageX;
                touches[eventType].y = touch.pageY;
                break;
              case 'touchend':
                touches[eventType] = true;
                if (debugMode) {
                  info.innerHTML = eventType
                }
                if (touches.touchstart.y > -1 && touches.touchmove.y > -1 && Math.abs(touches.touchstart.y - touches.touchmove.y) > window.innerHeight * 0.15) {
                  touches.direction = touches.touchstart.y < touches.touchmove.y ? "Up" : "Down";
                  if (debugMode) {
                    info.innerHTML += `\naction: moveSection${touches.direction} is ${typeof fpApi[`moveSection${touches.direction}`]}`
                  }
                  // DO STUFF HERE
                  if (fpApi) {
                    fpApi[`moveSection${touches.direction}`]()
                  }
                }
              default:
                break;
            }
          }
        }
      }
      e.ontouchstart = touchHandler('touchstart')
      e.ontouchmove = touchHandler('touchmove')
      e.ontouchend = touchHandler('touchend')
    }
  })
}
