const IndexPage = ({ location }) => {
  const fpApi = useRef()
  const [show, setShow] = useState()
  const [loaded, setLoaded] = useState()
  const [wave, setWave] = useState()
  const { setHeaderColor } = useHeader()
  useEffect(() => setTimeout(() => setShow(false), 1000), [show])
  useEffect(() => {
    if (fpApi.current) {
      fpApi.current.moveTo(location.hash.substr(1))
    }
  }, [location.hash])
  useEffect(() => {
    if(wave) {
      setTimeout(() => setWave(false), 1200)
    }
  }, [wave])
  // console.log(wave)
  return (
    <>
      <ReactFullpage
        anchors={anchors}
        //fullpage options
        licenseKey={process.env.GATSBY_FULLPAGE_JS_KEY}
        // onLeave={(origin, destination, direction) => {
        //   console.log(origin)
        //   //after leaving section 2
        //   if(origin.index == 1 && direction =='down'){
        //     fpApi.current.setAllowScrolling(false, 'down')
        //   }
        // }}
        afterRender={() => {
          handleInitalScroll(fpApi.current)
        }}
        afterResize={() => {
          handleInitalScroll(fpApi.current)
        }}
        scrollingSpeed={1000}
        normalScrollElements=".normalScroll"
        // afterLoad={() => {
        //   // animationFinished = false;
        //   if (silentMoving) {
        //     silentMoving = false
        //     justSilentMoved = true
        //     setTimeout(() => {
        //       justSilentMoved = false
        //     }, 500)
        //   }
        // }}
        onLeave={(origin, dest, dir) => {
            console.log(origin)
            if(dir === 'down' && (origin.index == 0 || origin.index == 4 || origin.index == 10)) {
            setWave(true)
          }
          if (dest.isLast) {
            setTimeout(() => {
              navigate('/about')
            }, 500)
            return false
          }
          return true
        }}
        render={({ state, fullpageApi }) => {
          fpApi.current = fullpageApi
          if (fullpageApi?.getActiveSection().index > 6 && fullpageApi?.getActiveSection().index < 11) {
            setHeaderColor('black')
          } else {
            setHeaderColor('white')
          }
          return (
            <ReactFullpage.Wrapper>
              <CoverPage onLoad={() => setLoaded(true)} fullpageApi={fullpageApi} isActive={fullpageApi?.getActiveSection().index == 0} />
              {pages.map((Comp, i) => (
                <Comp key={i} fullpageApi={fullpageApi} isActive={fullpageApi?.getActiveSection().index == (i + 1)} anchor={anchors[i + 1]} />
              ))}
              <Box className='section' anchor="jump" />
            </ReactFullpage.Wrapper>
          );
        }}
      />
      {wave && <Wave />}
      {/* <Wave /> */}
      <AnimatePresence>
        {!loaded && (
          <Loading />
        )}
      </AnimatePresence>
    </>
  )
}

export default IndexPage
