import React from 'react'
import {
  Modal,
  ModalCloseButton,
  ModalContent,
  ModalOverlay,
} from "@chakra-ui/react"
import GuidePage from '../../GuidePage'
import { responsive } from '../../../contexts/responsive'

const GuideModal = ({ isOpen, onOpen, species, setSpe }) => {
  return (
    <Modal size={''} isCentered isOpen={isOpen} onClose={() => onOpen(false)}>
      <ModalOverlay />
      <ModalContent borderRadius={'2em'} bg="custom.blue" width="90%">
        <GuidePage species={species} setSpe={setSpe} index={isOpen} />
        <ModalCloseButton index={10} fontSize={responsive('1.5em', '2em')} color="white" top="1em" right="1em" />
      </ModalContent>
    </Modal>
  )
}

export default GuideModal
