import { keyframes } from '@emotion/react';
import { StaticImage } from 'gatsby-plugin-image';
import styled from '@emotion/styled';
import React from 'react'
import Box from '../Box';

const swing = keyframes`
  100% {
    transform: translateX(50%);
  }
`

const move = keyframes`
  100% {
    top: -50%;
  }
`

const WaveAnime = styled(Box)`
  animation: ${swing} 0.8s;
`

const StyledWave = styled(Box)`
  position: fixed;
  background: #005B93;
  top: 100%;
  width: 100%;
  height: 100vh;
  animation: ${move} 1s linear;
`

const Wave = (props) => {

return (
  <StyledWave {...props}>
    <WaveAnime>
      <Box.Absolute left="-50%" right="-50%" bottom="95%">
        <StaticImage src="wave.png" />
      </Box.Absolute>
    </WaveAnime>
  </StyledWave>
)}

export default Wave
