import React, { useEffect, useState } from 'react'
import { Circle, IconButton } from '@chakra-ui/react'
import { StaticImage } from 'gatsby-plugin-image'
import { IoMdClose } from "react-icons/io";

import Box from '../../../components/Box'
import Flex from '../../../components/Flex'
import Text from '../../../components/Text'
import Title from '../../../components/Title';
import ArrowDown from '../../../components/ArrowDown'
import Bubbles from '../../../components/Bubbles';
import { Media } from '../../../contexts/responsive'

const equips = ['潛水裝備', '紀錄板、鉛筆', '手電筒、探棒', '固定繩', '100公尺穿越線', '浮力袋、線軸']
const roles = ['科學指導員｜至少1位', '佈線員｜2位', '魚類調查員｜2位', '無脊椎動物調查員｜2位', '底質調查員｜2位']

const Equipment = ({ fullpageApi, isActive }) => {
  const [show, setShow] = useState()
  const [guide, openGuide] = useState()
  const closeButton = (
    <IconButton
      _hover={{ bg: 'transparent' }}
      _active={{ bg: 'transparent' }}
      p="0"
      bg="transparent"
      color="black"
      pos="absolute"
      right="0em"
      top="0em"
      icon={<IoMdClose size="1.5em" />}
      onClick={(e) => {
        e.stopPropagation()
        openGuide(false)
      }}
    />
  )
  useEffect(() => {
    if(isActive) { setTimeout(() => setShow(true), 750)}
  } ,[isActive])
  return (
    <Box className='section' bg="custom.skyBlue">
      <Bubbles isActive={isActive} />
      <Flex pos="relative" as={Media} lessThan='md' px="1.5em">
        <Box.Relative cursor={'pointer'} onClick={() => openGuide('man')}>
          <StaticImage src="human.png" />
          {guide === 'man' && (
            <Box.Absolute zIndex={5} width="160%" bg="white" borderRadius="1em" py="1.875em" px="1.375em" top="50%" left="0">
              {closeButton}
              <Box.Inline fontSize="1em" borderRadius="full" bg="custom.equipBlue" py="0.25em" px={'1em'} color="white">所需角色（至少8位）</Box.Inline>
              {roles.map((d, i) => (
                <Text mt="0.5em" key={i}>{d}</Text>
              ))}
            </Box.Absolute>
          )}
        </Box.Relative>
        <Box.Relative cursor={'pointer'} onClick={() => openGuide('equip')}>
          <StaticImage src="equip.png" />
          {guide === 'equip' && (
            <Box.Absolute zIndex={5} width="100%" bg="white" borderRadius="1em" py="1.875em" px="1.375em" top="50%" right="0">
              {closeButton}
              <Box.Inline fontSize="1em" borderRadius="full" bg="custom.equipBlue" py="0.25em" px={'1em'} color="white">所需器材</Box.Inline>
              {equips.map((d, i) => (
                <Text mt="0.5em" key={i}>{d}</Text>
              ))}
            </Box.Absolute>
          )}
        </Box.Relative>
      </Flex>
      <Flex pos="relative" as={Media} greaterThanOrEqual='md' opacity={show ? 1 : 0} transition="all 1s" pl="12.75em" pr="10em" alignItems={'center'}>
        <Box width="36%">
          <Title>體檢計畫所需器材與人員</Title>
          <Box boxShadow={'-5px 3px 5px #CBDCEB'} borderRadius={'1em'} bg="white" px="2.5em" py="1.5em">
            <Box.Inline fontSize="1.875em" borderRadius="full" bg="custom.equipBlue" py="0.4em" px={'1.33em'} color="white">所需器材</Box.Inline>
            <Flex justifyContent={'space-between'} flexWrap="wrap">
              {equips.map((d, i) => (
                <Flex alignItems={'center'} mt="1.5em" width={'49%'} key={i}>
                  <Circle mr="0.25em" size="1em" fontSize={'2em'} bg="black" color="white">{i+1}</Circle>
                  <Text fontSize={'1.5em'}>{d}</Text>
                </Flex>
              ))}
            </Flex>
            <Box.Inline mt="1.5em" fontSize="1.875em" borderRadius="full" bg="custom.equipBlue" py="0.4em" px={'1.33em'} color="white">所需角色（至少8位）</Box.Inline>
            <Box>
              {roles.map((d, i) => (
                <Text fontSize={'1.5em'} mt="0.6em" key={i}>{d}</Text>
              ))}
            </Box>
          </Box>
        </Box>
        <Box ml="4.5em" width={'50%'}>
          <StaticImage src="equipment.png" />
        </Box>
      </Flex>
      <ArrowDown color="custom.skyBlue" fullpageApi={fullpageApi} />
    </Box>
  )
}

export default Equipment
